.header-container {
  /* width: 100%; */
  position: relative;
}

.background {
  height: 150px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.background-img {
  width: 100%;
  height: auto;
  object-fit:cover;
  transform: translate(-25%, -50%) rotate(-90deg);
}

.profile {
  width:125px;
  position: absolute;
  top:75px;
  left:15px;
  border-radius: 50%;
  aspect-ratio: 1;
  overflow: hidden;
  background-color: white;
  padding:3px;
}

.profile img {
  width: 100%;
  aspect-ratio: 1;
  border-radius: 50%;
}


.header-details {
  position: absolute;
  margin: 0;
  padding: 10px;
  top: 175px;
  left:0;
  color: black;
  text-wrap: nowrap;

}

.mt-0{
  margin-top: 0;
}

.profile-name {
  margin: 0 0 20px 0;
}

.details-info {
  display: flex;
  flex-direction: column;
}

.username p,
.workplace p,
.gender p,
.title p {
  margin: 0 0 10px 0;
  padding-right: 10px;
}




.username{
  flex-basis: 20%;
}

.title {
  flex-basis: 30%;
}

.workplace {
  flex-basis: 40%;
}

.gender {
  flex-basis: 10%;
}


.div-with-dot {
  position: relative;
  padding-right: 10px;
}

.div-with-dot::after {
  content: '';
  position: absolute;
  top: 40%;
  right: 10px;
  transform: translateY(-50%);
  height: 3px;
  width: 3px;
  background-color: gray; /* Adjust color to match your needs */
  border-radius: 50%; /* Make it a circle (dot) */
}

.flex-row {
  display: flex;
  flex-direction: row;

}


@media screen and (max-width:375px) {
 .background {
    height: 100px;
  }

  .profile {
    top:55px;
    width:75px;
  }

  .username p,
  .workplace p,
  .gender p,
  .title p {
    margin: 0 0 10px 0;
  }


